import { useState } from 'react';
import {Link} from 'react-router-dom'
import AuthInput from '../../components/Auth/AuthInput/AuthInput.jsx'
import AuthPageContainer from '../../components/Auth/AuthPageContainer/AuthPageContainer.jsx'
import Button from '../../components/Button/Button.jsx'

import styles from './SignUpPage.module.scss'

export default function SignUpPage () {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");

  return (
    <AuthPageContainer title=''>
      <AuthInput label='' value={account} placeholder='' onChange={(accountInputValue) => setAccount(accountInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='' value={name} placeholder='' onChange={(nameInputValue) => setName(nameInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='Email' value={email} placeholder='Email' onChange={(emailInputValue) => setEmail(emailInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='' type='password' value={password} placeholder='' onChange={(passwordInputValue) => setPassword(passwordInputValue)}
      notification='!' wordsLimit={20}
      />
      <AuthInput label='' type='password' value={checkPassword} placeholder='' onChange={(checkPasswordInputValue) => setCheckPassword(checkPasswordInputValue)}
      notification='!' wordsLimit={20}
      />
      <Button title='' size='large' isAction></Button>
      <div className={styles.link}>
        <Link to="/login">
          <span className={styles.span}></span>
        </Link>
      </div>
    </AuthPageContainer>
  );
}